import React, { Component } from 'react';
import AppBar from 'react-toolbox/lib/app_bar';
import styles from './App.pcss';
import { withRouter, Route, Switch } from "react-router-dom";
import Home from "components/pages/Home";
import Feed from "components/pages/Feed";
import TestsCreate from "components/tests/Create";
import TraitsCreate from "components/traits/Create";
import TestAmountsCreate from "components/testAmounts/Create";
import BiomarkerAmountsCreate from "components/biomarkerAmounts/Create";
import InterventionAmountsCreate from "components/interventionAmounts/Create";
import TheoriesShow from "components/theories/Show";
import TheoriesCreate from "components/theories/Create";
import IssuesShowAll from "components/issues/ShowAll";
import IssuesShow from "components/issues/Show";
import UsersShow from "components/users/Show";

import {IconMenu, MenuItem, MenuDivider } from 'react-toolbox/lib/menu';
import Navigation from 'react-toolbox/lib/navigation';

const MenuTest = ({goFeed, goCommunity}) => (
  <IconMenu icon='more_vert' position='topLeft' menuRipple>
    <MenuItem onClick={goCommunity} value='user' icon='people' caption='Community' />
    <MenuItem onClick={goFeed} value='rss_feed' icon='rss_feed' caption='Feed' />
    <MenuDivider />
    <MenuItem value='signout' caption='Logout' icon='delete' />
  </IconMenu>
);

const MenuAdd = ({go}) => (
  <IconMenu icon='add' position='topLeft' menuRipple>
    <MenuItem onClick={go('theories/create')} icon="add" value='theory' caption='Theory' />
    <MenuItem onClick={go('tests/create')}  icon="add" value='test' caption='Test' />
    <MenuItem onClick={go('traits/create')}  icon="add" value='trait' caption='Trait' />
    <MenuDivider />
    <MenuItem value='create' caption='Suggested Amounts' disabled />
    <MenuItem onClick={go('biomarker_amounts/create')}  icon="add" value='biomarkerAmount' caption='Biomarker Amount' />
    <MenuItem onClick={go('intervention_amounts/create')}  icon="add" value='interventionAmount' caption='Intervention Amount' />
    <MenuItem onClick={go('tests_amounts/create')}  icon="add" value='testFrequency' caption='Test Frequency' />
  </IconMenu>
);

class App extends Component {
  goHome = ()=>{
    this.props.history.push('/apoe4/')
  }
  goFeed = ()=>{
    this.props.history.push('/apoe4/feed')
  }
  go = (name)=>{
    return ()=>this.props.history.push(`/apoe4/${name}`)
  }
  goCommunity = ()=>{
    window.location.href = 'http://apoe4.info/forums';
  }
  render() {
    return (
      <div>
        <AppBar className={styles.appBar} title={<h1 onClick={this.goHome}>ApoE4</h1>}>
          <Navigation>
            <MenuAdd go={this.go} />
            <MenuTest goFeed={this.goFeed} goCommunity={this.goCommunity}/>
          </Navigation>
        </AppBar>
        <div className={styles.container}>
          <Switch>
            <Route exact path="/apoe4/" component={Home} />
            <Route exact path="/apoe4/traits/create" component={TraitsCreate} />
            <Route exact path="/apoe4/tests/create" component={TestsCreate} />
            <Route exact path="/apoe4/tests_amounts/create" component={TestAmountsCreate} />
            <Route exact path="/apoe4/intervention_amounts/create" component={InterventionAmountsCreate} />
            <Route exact path="/apoe4/biomarker_amounts/create" component={BiomarkerAmountsCreate} />
            <Route exact path="/apoe4/theories/create" component={TheoriesCreate} />
            <Route exact path="/apoe4/theories/:id" component={TheoriesShow} />
            <Route exact path="/apoe4/theories/:id/issues" component={IssuesShowAll} />
            <Route exact path="/apoe4/theories/:id/issues/:id" component={IssuesShow} />
            <Route exact path="/apoe4/users/:id" component={UsersShow} />
            <Route exact path="/apoe4/feed" component={Feed} />
          </Switch>
        </div>
      </div>
    );
  }
}

export default withRouter(App);



