import * as React from 'react';
import SVG from 'react-inlinesvg';
import styles from 'utils/style.pcss';
import { withRouter } from 'react-router-dom';

export const addClick = (selector, onClick)=>{
  document.querySelector(selector).addEventListener("click", onClick)
  document.querySelector(selector).style = "cursor: pointer;"
}


export const createPage = (selector, dest, svg, disableAddClick)=>withRouter(class extends React.PureComponent {
  onSVGLoad = ()=>{
    if (!disableAddClick)
      addClick(selector, ()=>this.props.history.push(`/apoe4${dest}`))
  }
  
  render(){
    return <div className={styles.svg}><SVG onLoad={this.onSVGLoad} src={svg} /></div>
  }
})

export const Clickable = ({children, ...props})=><span className={styles.clickable} {...props}>{children}</span>


