import svg from "./index2.svg"
import * as React from 'react';
import SVG from 'react-inlinesvg';
import styles from 'utils/style.pcss';
import { addClick, Clickable } from 'utils'
import { withRouter } from 'react-router-dom';
import classnames from 'classnames';
import homeStyles from './style.pcss';
import { Card, CardMedia, CardTitle, CardText, CardActions } from 'react-toolbox/lib/card';
import { Button } from 'react-toolbox/lib/button';
import { cidrSubnet } from "ip";

const codeNameMap = {
  Folate: {
    destination: 'theories/folate',
  },
  B12: {},
  Betaine: {},
  B6: {},
  Magnesium: {},
  Inflammation: {},
  Digestion: {},
  VitaminD: {
    title: 'Vitamin D'
  },
  ZincCopperRatio: {
    title: 'Zinc Copper Ratio'
  },
  'Anxiety&Stress': {
    title: 'Anxiety & Stress'
  },
  Homocysteine: {},
  'Homocysteine-Blood-Test': {},
  Cortisol: {},
  AD: {
    title: "Alzheimer's Disease",
  },
  Cardio: {
    title: "Cardiovascular Disease"
  },
  '-5-MTHF': {
    title: '5-MTHF'
  },
  'Folinic-acid': {
    title: 'Folinic Acid',
  },
  'Folic-Acid': {},
  'Folic-Acid-Default': {
    title: 'Default Suggested Amount',
    subtitle: 'Folic Acid',
    description: '6 x 10mg / week'
  },
  'Folinic-Acid-Default': {
    title: 'Default Suggested Amount',
    subtitle: 'Folinic Acid',
    description: '10 x 5mg / week'
  },
  '5MT-Default': {
    title: 'Default Suggested Amount',
    subtitle: '5-MTHF',
    description: '15 x 3mg / week'
  },
  'Folonic-Acid-C6777T': {
    title: 'Situation Amount',
    subtitle: 'Folinic Acid',
    description: 'Substitue Folinic Acid with 5-MTHF',
    situation: 'C6777T++'
  },
  'Homocysteine-Amount': {
    title: 'Default Suggested Amount',
    subtitle: 'Homocysteine',
    description: 'Less than 10-12mmol/L',
  }
}

export default withRouter(class extends React.PureComponent {
  state = {
    x: 0,
    y: 0,
    active: false,
    codeName: '',
  }
  onSVGLoad = ()=>{
    Object.keys(codeNameMap).forEach(codeName => {
      const selector = codeNameMap[codeName].selector || `[id^="${codeName}"]`
      const onClick = (event)=>{
        this.setState({
          x: event.pageX,
          y: event.pageY,
          active: true,
          codeName,
        })
      }
      addClick(`${selector} rect`, onClick);
      addClick(`${selector} text`, onClick);
    })
    // const h = document.getElementById('svg-holder').clientHeight;
    // const w = document.getElementById('svg-holder').clientWidth;
    // window.scrollTo(1500 / 2, 2500 / 2);
    window.scrollTo(658, 497);
  }
  closePopover = () => {
    this.setState({
      active: false,
    })
  }
  render(){
    const code = codeNameMap[this.state.codeName] || {};
    const title = code.title || this.state.codeName.split('-').join(' ');
    const description = code.description || 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nam gravida venenatis accumsan. In mi massa, tempus...'
    return <div>
      <div id="svg-holder"><SVG onLoad={this.onSVGLoad} src={svg} /></div>
      <Card className={classnames(homeStyles.popover)} style={{
        left: this.state.x,
        top: this.state.y,
        display: this.state.active ? 'block' : 'none',
      }}>
        <CardText>
          <Clickable>
            <p className={homeStyles.cardTitle} onClick={code.destination ? ()=>this.props.history.push(`/apoe4/${code.destination}`) : undefined}>{title}</p>
          </Clickable>
          {
            code.subtitle && <p className={homeStyles.subtitle}>{code.subtitle}</p> 
          }

        </CardText>
        <CardText>
          <p className={homeStyles.subtitle}>Description</p>
          <p>
            {
              description
            }
          </p>
        </CardText>
        {
          code.situation && <CardText>
          <p className={homeStyles.subtitle}>Situation</p>
          <p>
            {
              code.situation
            }
          </p>
        </CardText>
        }
        <CardActions>
          <Button label="Close" raised accent onClick={this.closePopover} />
        </CardActions>
      </Card>
    </div>
  }
})


